import { Component, OnInit } from '@angular/core';
import { SweetalertComponent } from 'app/shared/sweetalert/sweetalert.component';
import { OrderService } from '../../service/order.service';

@Component({
  selector: 'app-order-list',
  templateUrl: './order-list.component.html',
  styleUrls: ['./order-list.component.scss']
})
export class ListOrderComponent implements OnInit {
  public contentHeader: Object;
  public tableDataArray = [];

  constructor(
    private _sw: SweetalertComponent,
    private _serviceData: OrderService,
  ) { }

  ngOnInit(): void {
    this.getAllDetail();


    this.contentHeader = {
      headerTitle: "Invoice",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Order",
            isLink: true,
            link: "/",
          },
          {
            name: "List",
            isLink: false,
          },
        ],
      },
    };
  }


  getAllDetail() {
    this._serviceData.getAll().subscribe((res) => {
      if (res.data) {
        this.tableDataArray = res.data;
      }else if(!res.data && this.tableDataArray.length == 1){
        this.tableDataArray= []
      }
    });
  }

  confirmDelete(id,name,phone) {
    let deleteId = {
      id: id,
      customer_name:name,
      customer_phone:phone
    };
    this._sw.delete(() =>
      this._serviceData.delete(deleteId).subscribe(
        (res) => {
          setTimeout(() => this.getAllDetail(), 2000);
        },
        (err) => {
          this._sw.fire("error", "Collection", "There is a problem");
        }
      )
    );
  }


  searchTerm: string = '';
 get filterTableData() {
    if (!this.searchTerm) {
      return this.tableDataArray;
    }
    return this.tableDataArray.filter(item => item.customer_name.toLowerCase().includes(this.searchTerm.toLowerCase()));
  }

}
